document.addEventListener('DOMContentLoaded', () => {
    const deleteButtons = document.querySelectorAll('.alert2'); // Selecciona todos los botones de eliminaci√≥n

    deleteButtons.forEach(button => {
        button.addEventListener('click', function (e) {
            const form = this.closest('form'); // Obt√©n el formulario asociado al bot√≥n
            const itemId = form.querySelector('input[name="id"]').value; // Obt√©n el ID desde el campo oculto

            Swal.fire({
                title: "Al pulsar S√≠, el contenido ser√° eliminado, ¬øDesea confirmar?",
                showCancelButton: true,
                confirmButtonText: "S√≠",
                cancelButtonText: "No",
                confirmButtonColor: "#71B100",  // Color para el bot√≥n de confirmaci√≥n
                cancelButtonColor: "#F05454",   // Color para el bot√≥n de cancelaci√≥n
                backdrop: true,  // Oscurecer el fondo
                background: "#4e4d4b",  // Fondo personalizado de la alerta
                color: "#DDDDDD",        // Color del texto
                icon: "warning",
                customClass: {
                    popup: 'custom-popup',     // Clase personalizada para el popup
                    title: 'custom-title',     // Clase personalizada para el t√≠tulo
                    confirmButton: 'custom-button',  // Clase personalizada para el bot√≥n de confirmaci√≥n
                    cancelButton: 'custom-button'   // Clase personalizada para el bot√≥n de cancelaci√≥n
                }
            }).then((result) => {
                if (result.isConfirmed) {
                    form.submit(); // Env√≠a el formulario si el usuario confirma
                }
            });
        });
    });

    function mostrar_alerta() {
        Swal.fire({
            icon: "success",
            title: "¬°Hecho!"
        });
    }
});
