document.addEventListener('DOMContentLoaded', () => {
    // Selecciona el bot√≥n de reset
    const resetButton = document.querySelector('.alert3');

    // A√±ade un evento de clic al bot√≥n
    resetButton.addEventListener('click', function () {
        // Muestra el cuadro de confirmaci√≥n de SweetAlert
        Swal.fire({
            title: "Al pulsar S√≠, se eliminar√°n todos los registros, ¬øDesea continuar?",
            showCancelButton: true,
            confirmButtonText: "S√≠",
            cancelButtonText: "No",
            confirmButtonColor: "#71B100",  // Color para el bot√≥n de confirmaci√≥n
            cancelButtonColor: "#F05454",   // Color para el bot√≥n de cancelaci√≥n
            backdrop: true,  // Oscurecer el fondo
            background: "#4e4d4b",  // Fondo personalizado de la alerta
            color: "#DDDDDD",        // Color del texto
            icon: "warning",
            customClass: {
                popup: 'custom-popup',     // Clase personalizada para el popup
                title: 'custom-title',     // Clase personalizada para el t√≠tulo
                confirmButton: 'custom-button',  // Clase personalizada para el bot√≥n de confirmaci√≥n
                cancelButton: 'custom-button'   // Clase personalizada para el bot√≥n de cancelaci√≥n
            }

        }).then((result) => {
            if (result.isConfirmed) {
                // Si el usuario confirma, env√≠a el formulario
                const form = this.closest('form'); // Obt√©n el formulario asociado al bot√≥n
                form.submit(); // Env√≠a el formulario para hacer el reset
            }
        });
    });
});
